import React, {Component} from 'react';

import Aux from '../hoc/Aux'
import Layout from '../components/Layout/Layout'
import Jobs from '../components/Jobs/Jobs'
import * as Parser from 'rss-parser/index'

class App extends Component {
    state = {
        jobsData : {},
    };

    componentWillMount = () => {
        console.log("component did mount!")
    };

    componentDidMount = () => {

        (async () => {

            const rss = await new Parser();
            // https://stackoverflow.com/jobs/feed?l=Norway&u=Km&d=20&s=1&c=USD <-- Jobs with min salary of $1
            rss.parseURL('https://hnrss.org/newest')
                .then(feed => {
                    console.log(feed.items);
                    this.setState({
                        jobsData: feed.items
                    });
                }).catch(error => {
                    console.log(error)
            })

        })();
    }

    render() {
        return (
            <Aux>
                <Layout>
                    <Jobs jobsData={this.state.jobsData} />
                </Layout>
            </Aux>
        );
    }
}

export default App;



